.status {
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: bold;
    /* border: 2px solid blueviolet; */
  }
  
  .boardRow {
    display: flex;
  }
  
  .square {
    width: 60px;
    height: 60px;
    background-color: #fff;
    border: 1px solid #000000;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    line-height: 60px;
  }
  
  .square:focus {
    outline: none;
    background-color: #ddd;
  }
  
 